.contacts-content {
  margin-top: 20px;
  display: flex;
  justify-content: flex-start; }

.contacts-socials {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 30px; }
  .contacts-socials-button {
    border: 1px solid #464646; }
  .contacts-socials-button:hover, .contacts-socials-button:focus, .contacts-socials-button:active {
    color: white;
    border: 1px solid white;
    transform: rotate(360deg); }
